import { TreeModel } from './tree-model';
import { TREE_EVENTS } from '../constants/events';
import { METADATA_TYPE } from '../constants/metadata';

export class TreeNode {
    private _isExpanded = false; // flag of Expand or Collapse
    get isExpanded() { return this._isExpanded; } // 写法更简洁，更人性化
    set isExpanded(value) { this._isExpanded = true; }
    get isCollapsed() { return !this._isExpanded; }

    treeModel: TreeModel;
    parent: TreeNode;
    level: number;
    isVirtualRoot = false;

    _isActive = false;
    get isActive() {
        return this._isActive;
    }

    timerToggle: any;
    preventToggle = false;
    delay = 200;

    /**
     * 构建树模型
     * @param data
     * @param parent
     * @param treeModel
     * @param fileTreeDataService
     */
    constructor(data, parent: TreeNode = null, treeModel: TreeModel) {
        // 用于将对象可枚举属性的值复制到目标对象，并返回目标对象。
        // 如果目标对象中的属性具有相同的键，则属性将被源中的属性覆盖。
        Object.assign(this, data, { parent, treeModel });

        this.treeModel = treeModel;
        this.parent = parent;
        this.level = this.parent ? this.parent.level + 1 : 0;

        this.childrenField = this.childrenField.map(child => new TreeNode(child, this, treeModel));
    }

    // Proxy of treeModel
    get options() { return this.treeModel.options; }
    fireEvent(event) {
        this.treeModel.fireEvent(event);
    }

    get childrenField() {
        return this[this.options.childrenField] || [];
    }

    set childrenField(value) {
        this[this.options.childrenField] = value;
    }

    get displayField() {
        return this[this.options.displayField];
    }
    get pathField() {
        return this[this.options.pathField];
    }
    get fileTypeField() {
        return this[this.options.fileTypeField];
    }
    get treeNodeTemplate() {
        return this[this.options.treeNodeTemplate];
    }

    get isRoot() { return this.parent.isVirtualRoot; }
    get realParent() { return this.isRoot ? null : this.parent; }
    get isLeaf() { return !this.childrenField.length; }
    get hasChildren() { return !this.isFile || !(this.isLeaf); }
    get isFocused() { return this.treeModel.focusedNode === this; }

    get isProject() { return this.fileTypeField === METADATA_TYPE.PROJECT; }
    get isFolder() { return this.fileTypeField === METADATA_TYPE.FOLDER; }
    get isFile() { return this.fileTypeField === METADATA_TYPE.FILE; }

    get fileNodeType() {
        let fileType: string = null;
        switch (this.fileTypeField) {
            case METADATA_TYPE.PROJECT:
                fileType = 'project'; // 工程
                break;
            case METADATA_TYPE.FOLDER:
                fileType = 'folder'; // 文件夹
                break;
            default: // 默认是文件，返回具体的文件后缀
                fileType = 'file-';
                const fileSuffix = this.getFileSuffix(this.displayField);
                if (fileSuffix == null) {
                    fileType += 'default';
                } else {
                    fileType += fileSuffix;
                }
                break;
        }
        return fileType;
    }

    /**
     * 获取文件后缀名
     * @param fileName 文件名
     */
    getFileSuffix(fileName: string): string {
        if (undefined === fileName || null === fileName || fileName.length <= 0) {
            return null;
        }
        let fileSuffix: string;
        const names: string[] = fileName.split('.');
        switch (names.length) {
            case 0:
            case 1:
                fileSuffix = null;
                break;
            default:
                fileSuffix = names[names.length - 1];
                break;
        }

        return fileSuffix;
    }

    getFirstChild() {
        return this.childrenField && this.childrenField[0];
    }

    getLastChild() {
        return this.childrenField && this.childrenField[this.childrenField.length - 1];
    }

    private _getIndexInParent() {
        return this.parent && this.parent.childrenField.indexOf(this);
    }

    findAdjacentSibling(steps) {
        const index = this._getIndexInParent();
        return this.parent && this.parent.childrenField[index + steps];
    }

    findNextSibling() {
        return this.findAdjacentSibling(+1);
    }
    findPreviousSibling() {
        return this.findAdjacentSibling(-1);
    }

    findNextNode(goInside = true) {// 查找Next Node时，默认进行内部查找
        return goInside && this.isExpanded && this.getFirstChild() ||
            this.findNextSibling() ||
            this.parent && this.parent.findNextNode(false);
    }

    findPreviousNode() {
        const previousSibling = this.findPreviousSibling();
        if (!previousSibling) {
            return this.realParent;
        }
        return previousSibling.isCollapsed
            ? previousSibling
            : previousSibling.getLastChild();
    }

    // 切换方法
    // 切换节点的折叠（Collapsed）与扩展（Expanded）状态
    toggle(event?: Event) {
        const self = this;

        self.timerToggle = setTimeout(() => {
            // click 事件的处理
            if (!this.preventToggle) {
                self._isExpanded = !self.isExpanded;
                // focus时，触发load Children操作
                if (self.isExpanded && self.hasChildren) {
                    self.treeModel.dynamicLoadChildren(self, 1, true);
                }
                self.fireEvent({ eventName: TREE_EVENTS.onToggle, node: self, isExpanded: self.isExpanded });
            }

            this.preventToggle = false;
        }, self.delay);
    }

    private _activate() {
        this._isActive = true;
        // https://blog.csdn.net/qq_34645412/article/details/77823501
        this.fireEvent({ eventName: TREE_EVENTS.onActivate, node: this });
        this.focus();
    }

    private _deactivate() {
        this._isActive = false;
        this.fireEvent({ eventName: TREE_EVENTS.onDeactivate, node: this });
    }

    toggleActivated(event?: any) {

        if (this.isActive) {
            this._deactivate();
            this.treeModel.activeNode = null;
        } else {
            if (this.treeModel.activeNode) {
                this.treeModel.activeNode._deactivate();
            }
            this._activate();
            this.treeModel.activeNode = this;
        }
        this.fireEvent({ eventName: TREE_EVENTS.onActiveChanged, node: this, isActive: this.isActive });
    }

    focus() {
        const previousNode = this.treeModel.focusedNode;
        this.treeModel.focusedNode = this;
        if (previousNode) {
            this.fireEvent({ eventName: TREE_EVENTS.onBlur, node: previousNode });
        }
        this.fireEvent({ eventName: TREE_EVENTS.onFocus, node: this });
    }

    blur() {
        const previousNode = this.treeModel.focusedNode;
        this.treeModel.focusedNode = null;
        if (previousNode) {
            this.fireEvent({ eventName: TREE_EVENTS.onBlur, node: this });
        }
    }

    // 双击事件
    doublClick(rawEvent: MouseEvent) {
        // 在双击时，屏蔽产生的单击事件
        clearTimeout(this.timerToggle);
        this.preventToggle = true;

        // if (this.isFile || !this.hasChildren) {
            this.fireEvent({ eventName: TREE_EVENTS.onDoubleClick, node: this, rawEvent: rawEvent });
        // }
    }

    // 右键快捷菜单
    contextMenu(rawEvent: MouseEvent) {
        if (this.options.hasCustomContextMenu) {
            rawEvent.preventDefault();
        }
        this.fireEvent({ eventName: TREE_EVENTS.onContextMenu, node: this, rawEvent: rawEvent });
    }

    // 新增节点
    addTreeNode(addedNode: TreeNode) {
        if (this.isFile) {
            console.log('Can not add treeNode to a file node');
            return;
        }
        this.treeModel.addTreeNodeToServer(this, addedNode);
    }

    // 删除节点
    removeTreeNode(removedNode: TreeNode) {

    }
}



