/**
 * 节点重组规则
 */
export const NODE_REORGANIZER_RULE = {

    // 同名节点隶属规则
    AttachRule: [{
        // 节点名称类型划分:
        // a 和 b
        // a.x 和 b.x //仅考虑这种场景
        // a.b.x 和 a.c.x
        // ...
        
        // 唯一确定parent node: 节点名称 + 后缀
        // 分隔符为'.'，分割后长度为2，节点后缀为'hlp'，节点名称仅取分隔数组的第0个元素
        parentNode: {
            suffix: 'hlp'
            // // 以下属性强指定：
            // splitter: '.',
            // lengthAfterSplit: 2,
            // nameIndexAfterSplit: 0
        },
        // 和parent同名的children node
        // 分隔符为'.'或'_'，节点名称仅取分隔数组的第0个元素
        childrenNode: {
            splitter: '_'
            // // 以下属性强指定：
            // nameIndexAfterSplit: 0
        }},{
            parentNode: {
                suffix: 'hlp'
            },
            childrenNode: {
                splitter: '.'
            }
        },{
            parentNode: {
                suffix: 'cs'
            },
            childrenNode: {
                splitter: '.'
            }
        }
    ]
};


