    //         this.dynamicLoadChildren(tempParent, 1, true);
    //     }
    //     this.focusedNode = locatedNodeparent;

    //     // this.focusedNode = needLocatedNode;
    //     // let index = locatedNodeparent.childrenField.indexOf(needLocatedNode);
    //     // if(index < 0)
    //     // {
